<template>
<div class="wrap-cpa-push">
    <div class="cpa-form" > 
        <div class="search-bar">
            <span class="bar-name">合作平台</span>
            <el-input placeholder="搜索" v-model="cpaForm.cpaSearch" @change="doCpaSearch" size="small" clearable>
                <i slot="prefix" class="el-input__icon el-icon-search"></i>
            </el-input>
        </div> 
        <div class="checked-all" style="padding-left:118px;"> 
            <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
        </div>       
        <el-checkbox-group v-model="cpaForm.cpaList"  @change="handleCheckedChange">
            <div class="list-item" v-for="item in cpaSortList" :key="item.cate" v-if="item.item.length>0">
                <div class="item-label" >{{item.cate}}</div>
                <div class="item">
                    <el-checkbox :label="item2.code"  v-for="item2 in item.item" :key="item2.code" >
                    {{item2.name}}</el-checkbox>  
                </div>
            </div>         
        </el-checkbox-group>
    </div>
</div>
</template>
<script>
export default {
  data(){
      return{
          cpaForm:{
              cpaSearch:'',
              cpaList:[],
          },
          cpaSortListAll:[],
          cpaSortList:[],
          isIndeterminate:false,
          checkAll:false,

      }
  },
  props:["cpadata","cpaChecked"],
  methods: {
    init(){
       this.cpaForm= {
            cpaSearch:'',
            cpaList:[],
        }
        this.isIndeterminate=false;
        this.checkAll=false;
        this.doCpaSearch();
    },
    doCpaSearch(){
        let list=JSON.parse(JSON.stringify({key: this.cpaSortListAll})).key;
        for(let i in list){
            let it=list[i];
            let arr=it.item.concat([]);
            it.item=arr.filter(val=>{
                return val.name.toLowerCase().indexOf(this.cpaForm.cpaSearch.trim().toLowerCase())>-1
            })
        }
        this.cpaSortList=list;
        // this.handleCheckedChange(this.cpaForm.cpaList)
    },
    handleCheckAllChange(val){
        let arr=[];
        for(let i in this.cpaSortList){
            for(let j in this.cpaSortList[i].item){
                arr.push(this.cpaSortList[i].item[j].code)
            }
        }
        this.cpaForm.cpaList = val ? arr : [];
        this.isIndeterminate = false;
    },
    handleCheckedChange(value) {
        let arr=[];
        for(let i in this.cpaSortList){
            for(let j in this.cpaSortList[i].item){
                arr.push(this.cpaSortList[i].item[j].code)
            }
        }
        let checkedCount = value.length;
        this.checkAll = checkedCount === arr.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < arr.length;
    },
       
  },
  created(){
    if(this.cpadata.length>0){
        let list=this.cpadata.concat([]);
        let arr=[];
        this.cpaSortListAll=[];
        let arrUnique=[];
        for(let i in list){
            let item=list[i];
            item.show=true;
            let cate=item.name.split('-')[0];
            if(!arrUnique.includes(cate)){
                arrUnique.push(cate);
                arr.push({cate:cate,item:[]});
            }
            let index=arrUnique.findIndex(val=>{
                return val==cate
            });
            arr[index].item.push(item);             
        }
        this.cpaSortList=arr;
        this.cpaSortListAll=JSON.parse(JSON.stringify({key:arr})).key;
    
    }
   
    if(this.cpaChecked.length>0){
        this.init();
        this.cpaForm.cpaList=this.cpaChecked;
        this.isIndeterminate=true;
        this.checkAll = this.cpaChecked === this.cpadata.length;
    }else{
        this.init();
        this.cpaForm.cpaList=this.cpaChecked;
    }
      
  },
  watch:{
        cpaChecked(val){
            this.init();
            this.cpaForm.cpaList=val; 
            this.checkAll = this.cpaChecked === this.cpadata.length;
            this.isIndeterminate = this.cpaChecked > 0 && this.cpaChecked < this.cpadata.length;
        }
  }
}
</script>


<style lang="less" >
.cpa-form{
   
  .el-checkbox{
    margin-bottom:10px;
    width: 215px;
    margin-right: 10px;
  }
  .el-checkbox__label {
      font-size: 14px;
      padding-left: 5px;
      word-break: break-all;
      display: inline-block;
      width: 195px;
      height: auto;
      vertical-align: top;
      word-wrap: break-word;
      white-space: initial;
      text-align: left;    
  }
  .el-checkbox__input{
      vertical-align: top;
      margin-top:4px;
  }
  .list-item{
    margin-bottom:10px;
    position:relative;
    padding-left:120px;
    .item{
        padding:0;
    }
    .item-label{
      position:absolute;
      top:0px;
      left:10px;
      width:100px;
      word-break: break-all;
      word-wrap: break-word;
      white-space: initial;
      text-align: right;
      font-size: 12px;
      font-weight: 700;
      color: #222;
      margin-top: 0px;
      line-height: normal;

    }
  }
  .search-bar{
      .bar-name{
          width: 110px;
          display: inline-block;
          text-align: right;
      }
      margin-bottom: 15px;
      .el-input--small{
          width: 250px;
          margin-left: 6px;
      }
  }
  .el-checkbox+.el-checkbox{
      margin-left:0;
  }
}
</style>



