<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>合作商管理</el-breadcrumb-item>
                <el-breadcrumb-item>平台配置</el-breadcrumb-item>
            </el-breadcrumb>
            <!--新增-->
            <div class="add-menu fl-r">
                <el-button type="warning" class="add-menu-btn" @click="doAction('add')" size="mini" v-if="authOps.includes('/ec/ec-config/edit')">
                   新 增
                </el-button>
            </div>
        </div>
    
        <!--列表-->
        <el-table :data="dataList " class="report" stripe border style="width: 100%" v-loading="loading">
            <el-table-column label="scope Id" prop="scopeId" align="center" width="120"></el-table-column>
            <el-table-column label="scope" prop="scope" align="center" width="200"></el-table-column>
            <el-table-column label="路径" prop="path" align="center" width="120"></el-table-column>
            <el-table-column label="value值" prop="value" align="center"></el-table-column>
            <el-table-column label="描述" prop="description" align="center" width="200"></el-table-column>
            <el-table-column label="操作"  align="left" width="100">
                <template slot-scope="scope">
                    <el-button type="primary" size="mini" @click="doAction('edit',scope.row)"> 编 辑 </el-button>
                </template>
            </el-table-column>     
        </el-table>

        <el-dialog :title="dialogConfig.title" :visible.sync="dialogConfig.visible" width="50%">
            <div class="dialog-content">
                <el-form  :model="formConfig" class="demo-form-inline form-configf" label-width="120px" :inline="false" ref="configform">                    
                    <el-form-item label="scopeId" prop="scopeId" :rules="[{ required: true, message: '该字段必填'}]">
                        <el-input v-model="formConfig.scopeId" size="small" :disabled="dialogConfig.type=='edit'"></el-input>
                    </el-form-item>  
                    <el-form-item label="scope" prop="scope" :rules="[{ required: true, message: '该字段必填'}]">
                        <el-input v-model="formConfig.scope" size="small" :disabled="dialogConfig.type=='edit'"></el-input>
                    </el-form-item>  
                    <el-form-item label="路径" prop="path" :rules="[{ required: true, message: '该字段必填'}]">
                        <el-input v-model="formConfig.path" size="small" :disabled="dialogConfig.type=='edit'"></el-input>
                    </el-form-item>  
                    <el-form-item label="value值" prop="value" :rules="[{ required: true, message: '该字段必填'}]">
                        <el-input v-model="formConfig.value" rows="5" type="textarea" resize="vertical"></el-input>
                    </el-form-item>  
                    <el-form-item label="描述" prop="description" :rules="[{ required: true, message: '该字段必填'}]">
                        <el-input v-model="formConfig.description" rows="2" type="textarea"></el-input>
                    </el-form-item>  
                </el-form>
                
            </div>
            <div slot="footer" class="dialog-footer" v-if="authOps.includes('/ec/ec-config/edit')">
                <el-button @click="dialogConfig.visible = false" size="small">取 消</el-button>
                <el-button type="primary" @click="submit_dialog" size="small">确 定</el-button>
            </div>
        </el-dialog>
        <!--分页-->
    </div>
    
</template>


<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
            searchForm:{
                "scope":"",
                "scopeId":"",
                "path":""
            },
            //包裹列表
            loading: false,
            dataList: [],
            dialogConfig:{
                title:'',
                visible:false,
                type:''
            },
            formConfig:{
                "scope":"",
                "scopeId":"",
                "path":"",
                "description":"",
                "value":""
            }
            
        }
    },
    computed:{
        ...mapGetters({
            authOps: 'getAuthOps'
        })
    },  
    methods: {
        //获取cpa列表
        getDataList() {
            this.loading = true;
            this.axios.post('/proxy/ec/cpa/config/find', { body: this.searchForm }).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == '10000') {
                    this.dataList = data.body.config;
                }
            })
        },
        doAction(type,data){
            this.dialogConfig.type=type; 
            if(this.$refs.configform) this.$refs.configform.resetFields();
            switch(type){
                case 'add':
                    this.formConfig={
                        "scope":"",
                        "scopeId":"",
                        "path":"",
                        "decsription":"",
                        "value":""
                    }
                    this.dialogConfig.visible=true;
                break;
                case 'edit':
                    this.formConfig=JSON.parse(JSON.stringify(data))
                    this.dialogConfig.visible=true;
                break;
            }
        },
        submit_dialog(){
            this.$refs.configform.validate(res=>{
                if(res){
                   this.axios.post('/proxy/ec/cpa/config/save',{body:this.formConfig}).then(res=>{
                       let data=res.data;
                       if(data.head.code=='10000'){
                           this.dialogConfig.visible=false;
                           this.$message.success('保存成功');
                           this.getDataList();
                       }
                   }) 
                }
            })

        }
    },
    created() {
        this.getDataList()
    }

}
</script>




