<template>
    <div class="wrap-commission-rate-supplier">
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">                   
                <el-form-item label="商家编码">
                    <el-input v-model.trim="searchForm.supplierCode" size="small"></el-input>
                </el-form-item>
                <el-form-item label="商家名称">
                    <el-input v-model.trim="searchForm.supplierName" size="small"></el-input>
                </el-form-item>
                <el-form-item label="CPA店铺">
                    <el-select v-model="searchForm.cpaCode" size="small" clearable filterable>
                        <el-option :value="item.code" :label="item.name" v-for="item in cpaList" :key="item.code"></el-option>
                    </el-select>
                </el-form-item>                        
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt"> 查 询 </el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="chunk-batch" v-if="authOps.includes('/ec/commission-rate/action')">
             <el-button type="success" class="add-menu-btn" size="mini" @click="doAction('add')"> 新 增 </el-button>
        </div>
        <el-dialog :title="dialogSupplier.title" :visible.sync="dialogSupplier.visible" width="75%">
            <div class="dialog-content">
                <el-form  :model="formSupplier" class="demo-form-inline form-supplier" label-width="120px" :inline="false" ref="supplierform">                    
                    <el-form-item label="商家编码" prop="supplierCode" :rules="[{ required: true, message: '该字段必填'}]">
                        <el-input v-model="formSupplier.supplierCode" size="small" :disabled="this.dialogSupplier.type=='edit'"></el-input>
                    </el-form-item>  
                    <el-form-item label="降价比例" prop="commissionRate" :rules="[{ required: true, message: '该字段必填'},{pattern: /(^([1-9]\d*(\.\d{1,2})?)$)|(^(0\.[1-9][0-9]{0,1})$)|(^(0\.0[1-9])$)/, message: '请输入大于零的两位小数'}]">
                        <el-input v-model="formSupplier.commissionRate" size="small">
                             <i slot="suffix" class="">%</i>
                        </el-input>
                       <div class="form-tip">请输入小于丰卖佣金比例内的值，保留两位小数</div>
                    </el-form-item>
                    
                    <div>
                        <el-form-item label="有效期"  prop="date" :rules="[{ required: true, message: '该字段必填                               '}]">
                            <el-date-picker
                            size="small"
                            v-model="formSupplier.date"
                            type="daterange"
                            align="right"
                            unlink-panels
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期">
                            </el-date-picker>
                        </el-form-item> 
                    </div>  
 
                </el-form>
                <cmp-cpa ref="cpa" :cpadata="cpaList" :cpa-checked="cpaChecked"></cmp-cpa> 

            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogSupplier.visible = false" size="small">取 消</el-button>
                <el-button type="primary" @click="submit_dialog" size="small">确 定</el-button>
            </div>
        </el-dialog>

        <!-- //表格数据 -->
        <el-table :data="tableList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" :tooltip-effect="'light'" highlight-current-row border>
            <el-table-column label="商家编码" prop="supplierCode" align="center" width="150" show-overflow-tooltip fixed="left" ></el-table-column>
            <el-table-column label="商家名称" prop="supplierName" align="center" min-width="150" show-overflow-tooltip ></el-table-column>
            <el-table-column label="降价比例" align="center" width="150" show-overflow-tooltip >
                <template slot-scope="scope">{{scope.row.commissionRate/100}}%</template>
            </el-table-column>
            <el-table-column label="有效期"  align="center" width="180" show-overflow-tooltip >
                <template slot-scope="scope">{{scope.row.startEffectiveDate}} &nbsp;&nbsp;&nbsp;{{scope.row.endEffectiveDate}}</template>
            </el-table-column>
            <el-table-column label="CPA店铺" prop="cpaNames" align="center" min-width="150" show-overflow-tooltip ></el-table-column>
            <el-table-column label="最近修改人" prop="updateUserName" align="center" width="150" show-overflow-tooltip ></el-table-column>
            <el-table-column label="最近修改时间" prop="updateTime" align="center" width="150" show-overflow-tooltip ></el-table-column>
            <el-table-column label="操作"  align="left" width="150" show-overflow-tooltip :fixed="'right'" class-name="do-action" v-if="authOps.includes('/ec/commission-rate/action')">
                <template slot-scope="scope">
                    <span @click="doAction('edit',scope.row)">编辑</span>
                    <span @click="doAction('delete',scope.row)">删除</span>
                </template>
            </el-table-column>
            
        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
import CmpCpa from "@/components/sys/goods/cpa-push.vue";
import { mapGetters } from 'vuex';
export default {
    components:{
        CmpCpa
    },
    data(){
        return {
            searchForm:{
                supplierCode:'',
                supplierName:'',
                cpaCode:'',
                cpaName:'',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },
            tableList:[],
            total:'',
            loading:false,
            dialogSupplier:{
                visible:false,
                title:"",
                url:'',
                type:''
            },
            formSupplier:{
                commissionId:'',
                supplierCode:"",
                commissionRate:"",
                startEffectiveDate:"",
                endEffectiveDate:"",
                cpaCodes:[],
                date:''
            },
            cpaChecked:[]
        }
    },
    props:['cpaList'],
    computed:{
        ...mapGetters({
            authOps: 'getAuthOps'
        })
    },        
    methods:{
        search_smt() {
            this.searchForm.page.page = 1;
            // this.DateToString();
            this.getTableList();
        },
        //获取表格数据
        getTableList(){
            this.loading=true;
            this.axios.post("/proxy/ec/commission/getCommissionSubsidyListBySupplier",{body:this.searchForm}).then(res=>{
                this.loading=false;
                let data=res.data;
                if(data.head.code=="10000"){
                    this.tableList=data.body.rows;
                    this.total = data.body.total;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getTableList();
        },
        doAction(type,data){
            this.dialogSupplier.type=type;
            switch(type){
                case 'add':
                    this.dialogSupplier.title="商家维度调价比例新增";
                    this.dialogSupplier.visible=true;
                     // 初始化数据
                    this.formSupplier={
                        commissionId:'',
                        supplierCode:"",
                        commissionRate:"",
                        startEffectiveDate:"",
                        endEffectiveDate:"",
                        cpaCodes:[],
                        date:''
                    };
                    this.cpaChecked=[];
                    this.dialogSupplier.url="/proxy/ec/commission/addCommissionSubsidyBySupplier";
                    
                break;
                case 'edit':
                    // 初始化数据
                    this.dialogSupplier.title="商家维度调价比例修改";
                    this.dialogSupplier.visible=true;
                    this.formSupplier.commissionId=data.commissionId;
                    this.formSupplier.supplierCode=data.supplierCode;
                    this.formSupplier.commissionRate=data.commissionRate/100;
                    this.formSupplier.date=[data.startEffectiveDate,data.endEffectiveDate];
                    this.cpaChecked=data.cpaCodes.split(',').map(item=>{
                        return parseInt(item)
                    })
                    this.dialogSupplier.url="/proxy/ec/commission/updateCommissionSubsidyById";                                
                break;
                case "delete":
                    this.$confirm('确定要删除', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.axios.post("/proxy/ec/commission/deleteCommissionSubsidyById",{body:{commissionId:data.commissionId}}).then(res=>{
                            let data=res.data;
                            if(data.head.code=="10000"){
                                this.$message.success("删除成功");
                                this.getTableList();
                            }
                        })
                    }).catch(() => {
                        this.$message.info("已取消删除");         
                    });                    
                break;
            }
        },
        submit_dialog(){
            this.$refs.supplierform.validate(validate=>{
                if(!validate) return false
                if(this.$refs.cpa.cpaForm.cpaList.length==0){
                    this.$message.error("请选择CPA店铺");
                    return false
                }
                let params=JSON.parse(JSON.stringify(this.formSupplier));
                if(params.commissionRate<0||params.commissionRate>7.5){
                    this.$message.error("降价比例范围(0-7.5%)")
                    return false
                }
                params.commissionRate=parseFloat(params.commissionRate)*100;
                params.startEffectiveDate=this.format("yyyy-MM-dd",new Date(this.formSupplier.date[0]));
                params.endEffectiveDate=this.format("yyyy-MM-dd",new Date(this.formSupplier.date[1]));
                params.cpaCodes=this.$refs.cpa.cpaForm.cpaList;
                this.axios.post(this.dialogSupplier.url,{body:params}).then(res=>{
                    let data=res.data;
                    if(data.head.code=="10000"){
                        this.$message.success("保存成功");
                        this.dialogSupplier.visible=false;
                        this.getTableList();
                    }
                })
            })
            
            
        }

    },
    created(){       
        this.getTableList();
    }
}
</script>

<style lang="less" scoped>
.search{
    padding-bottom: 10px;
}
.chunk-batch{
    margin-bottom:10px;
}
.do-action{
    span{
        color: #0F3FCF;
        cursor: pointer;
        margin:0 10px;
    }
}

</style>
<style lang="less">
.form-supplier{
    
    .el-date-editor .el-range-separator{
        line-height: 24px;
    }
    
}
.el-dialog__body{
    max-height: 450px;
    overflow-y:auto;
    padding: 0 20px;
}
.el-dialog{
    margin-bottom:10px;
}

</style>





