<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>{{partners}}</el-breadcrumb-item>
                <el-breadcrumb-item>商品列表</el-breadcrumb-item>
            </el-breadcrumb>
            <!--新增-->
            <div class="add-menu fl-r">
                <el-button type="warning" class="add-menu-btn" @click="addGoods()" size="mini">
                    <i class="iconfont icon-add"></i>添加商品
                </el-button>
            </div>
        </div>

        <!-- 搜索 -->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">
                <el-form-item label="商品名称">
                    <el-input v-model="searchForm.goodsName" placeholder="商品名称" size="small"></el-input>
                </el-form-item>
                <el-form-item label="商品编码">
                    <el-input v-model="searchForm.goodsSn" placeholder="商品编码" size="small"></el-input>
                </el-form-item>
                <el-form-item label="最低售价">
                    <el-input v-model="searchForm.minshopPrice" placeholder="最低售价" size="small"></el-input>
                </el-form-item>
                <el-form-item label="最高售价">
                    <el-input v-model="searchForm.maxshopPrice" placeholder="最高售价" size="small"></el-input>
                </el-form-item>
                <!-- <el-form-item label="物流方式">
                    <el-select v-model="searchForm.logisticsType" size="small" clearable >                              
                        <el-option :value="item.logisticsCode" :label="item.logisticsName" v-for="item in logisticsList" :key="item.logisticsCode"></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="品牌">
                    <el-autocomplete v-model="searchForm.brandName" :fetch-suggestions="remoteGetBrand" placeholder="请输入品牌" :trigger-on-focus="false" size="small" :value-key="'brandName'" @select="brandSelect"></el-autocomplete>
                </el-form-item>
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt">查询</el-button>
                </el-form-item>
            </el-form>
        </div>

        <!--商品列表-->
        <div class="basth">
            <el-button type="primary" size="small" @click="doAction('')">批量删除</el-button>
        </div>
        <el-table :data="cpaGoodsList" stripe style="width: 100%" v-loading="loading" @selection-change="handleSelectionChange" class="report" border>
            <el-table-column type="selection" width="55"></el-table-column>
            <el-table-column label="流水号" prop="id" align="left" width="100" show-overflow-tooltip></el-table-column>
            <el-table-column label="商品编码" prop="goodsSn" align="left" width="150" show-overflow-tooltip></el-table-column>
            <el-table-column label="商品名称" prop="goodsName" align="left" min-width="300" > </el-table-column>
            <el-table-column label="品牌名称" prop="brandName" align="left" width="180" show-overflow-tooltip> </el-table-column>
            <!-- <el-table-column label="物流方式" prop="logisticsTypeName" align="left" min-width="150"> </el-table-column> -->

            <el-table-column label="售价(美元)" prop="shopPrice" width="120" align="right"> 
                <template slot-scope="scope">{{scope.row.shopPrice?(scope.row.shopPrice/100).toFixed(2):''}}</template>
            </el-table-column>
            <el-table-column label="操作" width="100" >
                <template slot-scope="scope">
                    <i title="删除" class="danger iconfont icon-delete" @click="doAction(scope.row.id)"></i>
                </template>
            </el-table-column>
        </el-table>
        <!--分页-->

        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page" :page-size="searchForm.rows" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>


<style lang="less" scoped>
.wrap {
    padding-right: 40px;
    .basth {
        padding-bottom: 10px;
    }
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            searchForm: {
                goodsName: '',
                goodsSn: '',
                cpaCode: '',
                brandId: '',
                brandName: '',
                minshopPrice: '',
                maxshopPrice: '',
                logisticsType:'',
                offset: 0,
                page: 1,
                rows: this.myConfig.pageRows
            },
            //品牌
            brandsList: [],
            optionList: [],
            //合作商
            cpaList: [],
            //包裹列表
            loading: false,
            brandLoading: false,
            cpaGoodsList: [],
            total: 0,
            //选择数据
            selectData: [],
            brandProps: {
                value: 'brandName',
                label: 'brandName'
            },
            //物流方式
            logisticsList:[]
        }
    },
    computed: {
        partners() {
            for (let i in this.cpaList) {
                if (this.cpaList[i].code == this.searchForm.cpaCode) {
                    return this.cpaList[i].name
                }
            }

        }
    },
    methods: {
        //获取包裹列表
        getcpaGoodsList() {
            if (this.searchForm.brandName == '') {
                this.searchForm.brandId = '';
            }
            this.loading = true;
            let params=JSON.parse(JSON.stringify(this.searchForm));
            params.minshopPrice=parseFloat(params.minshopPrice)*100;
            params.maxshopPrice=parseFloat(params.maxshopPrice)*100;
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/goods/self', { body:params }).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cpaGoodsList = data.body.list;
                    this.total = data.body.total;
                }
            })
        },
        // 获取品牌列表
        getBrands() {
            this.axios.post(this.myConfig.code.proxy + '/goods/api/brand/list', { body: {} }).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == '10000') {
                    this.brandsList = data.body.rows;
                }
            })
        },
        //获取cpa列表
        getcpaList() {
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/partners/all', { body: this.searchForm }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cpaList = data.body.list;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.offset = (val - 1) * this.searchForm.rows;
            this.searchForm.page = val;
            this.getcpaGoodsList();
        },
        search_smt() {
            this.searchForm.page=1;
            this.searchForm.offset=0;
            this.getcpaGoodsList();
        },
        doAction(id) {
            let _this = this;
            let ids = [];
            if (id != '') {
                ids = [id];
            } else {
                if (this.selectData.length == 0) {
                    this.$message({
                        message: '请选择产品',
                        type: 'error'
                    })
                    return false;
                } else {
                    for (let i in this.selectData) {
                        ids.push(this.selectData[i].id)
                    }
                }
            }
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/goods/del', { body: { cpaCode: this.searchForm.cpaCode, ids: ids.join(',') } }).then(response => {
                let data = response.data;
                if (data.head.code == '10000') {
                    this.$message({
                        message: '删除成功！',
                        type: 'success'
                    })
                    this.getcpaGoodsList();
                }
            })
        },
        //复选框
        handleSelectionChange(val) {
            this.selectData = val;
        },
        addGoods() {
            this.$router.push('/ec/cpa-goods-add/' + this.searchForm.cpaCode);
        },
        //品牌搜索
        remoteGetBrand(queryString, cb) {
            let restaurants = this.brandsList;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : [];
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        createFilter(queryString) {
            return (restaurant) => {
                return (restaurant.brandName.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };

        },
        brandSelect(item) {
            this.searchForm.brandId = item.brandId;
        },// 获取物流方式
        getLogistics() {
        this.axios.post(this.myConfig.code.proxy + "/logistics/logistics/queryType", { body: { languageCode: "cn", status: 0 } }).then(response => {
            let data = response.data;
            if (data.head.code == "10000") {
                this.logisticsList = data.body.logisticsTypes;
            }
            });
        },
        
    },
    watch: {
        $route() {
            this.searchForm = {
                goodsName: '',
                goodsSn: '',
                cpaCode: '',
                brandName: '',
                brandId: '',
                minshopPrice: '',
                maxshopPrice: '',
                logisticsType:'',
                offset: 0,
                page: 1,
                rows: this.myConfig.pageRows
            };
            this.searchForm.cpaCode = this.$route.params.id;
            this.getcpaList();
            this.getBrands();
            this.getcpaGoodsList();
            this.getLogistics();
        }
    },
    created() {
        this.searchForm.cpaCode = this.$route.params.id;
        this.getcpaList();
        this.getBrands();
        this.getcpaGoodsList();
        // this.getLogistics();
    }

}
</script>




