<template>
    <div class="wrap">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>合作商管理</el-breadcrumb-item>
                <el-breadcrumb-item>合作商管理列表</el-breadcrumb-item>
            </el-breadcrumb>
        </div>
    
        <!--列表-->
        <el-table :data="cpaList" stripe style="width: 100%" v-loading="loading">
            <el-table-column label="合作商编号" prop="code" align="center"></el-table-column>
            <el-table-column label="合作商名称" prop="name" align="center"></el-table-column>
            <el-table-column label="币种" prop="currency" align="center"></el-table-column>
            <el-table-column label="是否激活" prop="active" align="center"></el-table-column>
            <el-table-column label="接入方式" prop="isThirdPartyApi" align="center"></el-table-column>
            <el-table-column label="通信密钥" prop="authToken" align="center"></el-table-column>
        </el-table>
        <!--分页-->
    </div>
</template>


<style lang="less" scoped>
.wrap {
    padding-right: 40px;
}
</style>

<script>
import format from 'date-format';
export default {
    data() {
        return {
            
            //包裹列表
            loading: false,
            cpaList: [],
            
        }
    },

    methods: {
        //获取cpa列表
        getcpaList() {
            this.loading = true;
            this.axios.post(this.myConfig.code.proxy + '/ec/cpa/partners/all', { body: this.searchForm }).then(response => {
                this.loading = false;
                let data = response.data;
                if (data.head.code == '10000') {
                    this.cpaList = data.body.list;
                }
            })
        }
    },
    created() {
        this.getcpaList()
    }

}
</script>




