<template>
    <div class="wrap-commission-rate-supplier">
        <!--搜索-->
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="100px">                   
                <el-form-item label="商品ID">
                    <el-input v-model.trim="searchForm.goodsId" size="small"></el-input>
                </el-form-item>
                <el-form-item label="商品编码">
                    <el-input v-model.trim="searchForm.goodsSn" size="small"></el-input>
                </el-form-item>
                <el-form-item label="商品名称">
                    <el-input v-model.trim="searchForm.goodsName" size="small"></el-input>
                </el-form-item>
                <el-form-item label="商家编码">
                    <el-input v-model.trim="searchForm.supplierCode" size="small"></el-input>
                </el-form-item>
                <el-form-item label="CPA店铺">
                    <el-select v-model="searchForm.cpaCode" size="small" clearable filterable>
                        <el-option :value="item.code" :label="item.name" v-for="item in cpaList" :key="item.code"></el-option>
                    </el-select>
                </el-form-item>                        
                <el-form-item class="search-btn">
                    <el-button type="primary" @click="search_smt" size="small"> 查 询 </el-button>
                </el-form-item>
            </el-form>
        </div>
        <div class="chunk-batch" v-if="authOps.includes('/ec/commission-rate/action')">
             <el-button type="success" class="add-menu-btn" size="mini" @click="doAction('add')"> 新 增 </el-button>
        </div>
        <el-dialog :title="dialogGoods.title" :visible.sync="dialogGoods.visible" width="85%" :class="dialogGoods.type=='add'?'dialog-goods':'dialog-goods edit'">
            <div class="dialog-content">
                <div class="step-box" v-if="dialogGoods.type=='add'" >
                    <el-steps :active="dialogGoods.step" finish-status="success">
                        <el-step title="选择商品"></el-step>
                        <el-step title="设置比例及有效期"></el-step>
                        <el-step title="完成"></el-step>
                    </el-steps>
                </div>
                <div class="step-1" v-show="dialogGoods.step==0">
                    <div class="search report">
                        <el-form :inline="true" :model="searchGoods" class="form-goods-select" label-width="100px">                   
                            <el-form-item label="商品ID">
                                <el-input v-model.trim="searchGoods.goodsId" size="small"></el-input>
                            </el-form-item>
                            <el-form-item label="商品名称">
                                <el-input v-model.trim="searchGoods.goodsName" size="small"></el-input>
                            </el-form-item>
                            <el-form-item label="商家编码">
                                <el-input v-model.trim="searchGoods.supplierCode" size="small"></el-input>
                            </el-form-item>                      
                            <el-form-item class="search-btn">
                                <el-button type="primary" @click="search_smt_goods" size="small"> 查 询 </el-button>
                            </el-form-item>
                        </el-form>
                    </div>
                    <el-table :data="goodsList" stripe style="width: 100%" v-loading="goodsLoading" class="report" max-height="320" :tooltip-effect="'light'" highlight-current-row border @selection-change="handleSelectionChange" ref="tableGoods">
                        <el-table-column  width="50" type="selection" :fixed="'left'"></el-table-column>
                        <el-table-column label="商家编码" prop="supplierCode" align="center" width="150" show-overflow-tooltip :fixed="'left'"></el-table-column>
                        <el-table-column label="商品ID" prop="goodsId" align="center" min-width="150" show-overflow-tooltip ></el-table-column>
                        <el-table-column label="商品编码" prop="goodsSn" align="center" min-width="150" show-overflow-tooltip ></el-table-column>
                        <el-table-column label="商品名称" prop="goodsName" align="center" min-width="200" show-overflow-tooltip ></el-table-column>                  
                    </el-table>
                    <!-- 分页 -->
                    <div class="pagination" >
                        <el-pagination @current-change="handleCurrentChangeGoods" @size-change="handleSizeChange" :current-page="searchGoods.page.page" :page-size="searchGoods.page.pageSize" layout="total,sizes, prev, pager, next, jumper" :total="goodsTotal" :page-sizes="[20,50,100,200,500]">
                        </el-pagination>
                        <el-button type="success" plain size="mini" class="btn-next"  @click="doAction('stepNext')"> 下 一 步 </el-button>
                    </div>                
                </div>
                <div class="step-2" v-show="dialogGoods.step==1">
                    <el-form  :model="formGoodsComRate" class="demo-form-inline form-supplier" label-width="120px" :inline="false" ref="goodsform">                    
                        <div class="commission-info" v-if="dialogGoods.type=='edit'">
                            <el-form-item label="商家编码:"  >{{formGoodsComRate.supplierCode}}</el-form-item>
                            <el-form-item label="商品ID:"  >{{formGoodsComRate.goodsId}}</el-form-item>
                            <el-form-item label="商品编码:" >{{formGoodsComRate.goodsSn}}</el-form-item>
                        </div>
                        <div>  
                            <el-form-item label="降价比例:" prop="commissionRate" :rules="[{ required: true, message: '该字段必填'},{pattern: /(^([1-9]\d*(\.\d{1,2})?)$)|(^(0\.[1-9][0-9]{0,1})$)|(^(0\.0[1-9])$)/, message: '请输入大于零的两位小数'}]">
                                <el-input v-model="formGoodsComRate.commissionRate" size="small">
                                    <i slot="suffix" class="">%</i>
                                </el-input> 
                                 <div class="form-tip">请输入小于丰卖佣金比例内的值，保留两位小数</div>                       
                            </el-form-item>
                                                 
                            <el-form-item label="有效期:"  prop="date" :rules="[{ required: true, message: '该字段必填'}]">
                                <el-date-picker  size="small"    v-model="formGoodsComRate.date"  type="daterange"  align="right"  unlink-panel range-separator="至"   start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
                            </el-form-item> 
                        </div>      
    
                    </el-form>
                    <cmp-cpa ref="cpa" :cpadata="cpaList" :cpa-checked="cpaChecked"></cmp-cpa>                     
                </div>

            </div>
            <div slot="footer" class="dialog-footer" v-show="dialogGoods.step==1">
                <el-button type="success" plain @click="dialogGoods.step=0" size="mini" v-if="dialogGoods.type=='add'"> 上一步 </el-button>
                <el-button type="primary" plain @click="submit_dialog" size="mini"> 保存 </el-button>
            </div>
        </el-dialog>

        <!-- //表格数据 -->
        <el-table :data="tableList" stripe style="width: 100%" v-loading="loading" class="report" max-height="520" :tooltip-effect="'light'" highlight-current-row border>
            <el-table-column label="商家编码" prop="supplierCode" align="center" width="150" show-overflow-tooltip :fixed="'left'"></el-table-column>
            <el-table-column label="商品ID" prop="goodsId" align="center" min-width="150" show-overflow-tooltip ></el-table-column>
            <el-table-column label="商品编码" prop="goodsSn" align="center" min-width="150" show-overflow-tooltip ></el-table-column>
            <el-table-column label="商品名称" prop="goodsName" align="center" min-width="200" show-overflow-tooltip ></el-table-column>
            <el-table-column label="降价比例" align="center" width="150" show-overflow-tooltip >
                <template slot-scope="scope">{{scope.row.commissionRate/100}}%</template>
            </el-table-column>
            <el-table-column label="有效期"  align="center" width="180" show-overflow-tooltip >
                <template slot-scope="scope">{{scope.row.startEffectiveDate}} &nbsp;&nbsp;&nbsp;{{scope.row.endEffectiveDate}}</template>
            </el-table-column>
            <el-table-column label="CPA店铺" prop="cpaNames" align="center" min-width="150" show-overflow-tooltip ></el-table-column>
            <el-table-column label="最近修改人" prop="updateUserName" align="center" width="150" show-overflow-tooltip ></el-table-column>
            <el-table-column label="最近修改时间" prop="updateTime" align="center" width="150" show-overflow-tooltip ></el-table-column>
            <el-table-column label="操作"  align="left" width="150" show-overflow-tooltip :fixed="'right'" class-name="do-action" v-if="authOps.includes('/ec/commission-rate/action')">
                <template slot-scope="scope">
                    <span @click="doAction('edit',scope.row)">编辑</span>
                    <span @click="doAction('delete',scope.row)">删除</span>
                </template>
            </el-table-column>
            
        </el-table>
        <!-- 分页 -->
        <div class="pagination" v-if="Math.ceil(total/myConfig.pageRows)>1">
            <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
import CmpCpa from "@/components/sys/goods/cpa-push.vue";
import { mapGetters } from 'vuex';
export default {
    components:{
        CmpCpa
    },
    data(){
        return {
            searchForm:{
                supplierCode:'',
                supplierName:'',
                cpaCode:'',
                cpaName:'',
                page: {
                    page: 1,
                    pageSize: this.myConfig.pageRows
                }
            },
            tableList:[],
            total:'',
            loading:false,
            dialogGoods:{
                visible:false,
                title:"",
                url:'',
                type:'',
                step:0
            },
            
            cpaChecked:[],
            //商品选择
            searchGoods:{
                supplierCode:'',
                goodsId:'',
                goodsSn:'',
                page: {
                    page: 1,
                    pageSize: 20
                }
            },
            goodsList:[],
            goodsTotal:'',
            goodsLoading:false,
            selectedGoods:[],//选择的商品
            formGoodsComRate:{
                goodsIds:[],
                commissionRate:'',
                date:'',
                startEffectiveDate:'',
                endEffectiveDate:'',
                cpaCodes:[],
                commissionId:'',//编辑
                supplierCode:'',
                goodsId:'',
                goodsSn:'',
                

            },
            cpaChecked:[]
            
        }
    },
    props:['cpaList'],
    computed:{
        ...mapGetters({
            authOps: 'getAuthOps'
        })
    },        
    methods:{
        search_smt() {
            this.searchForm.page.page = 1;
            // this.DateToString();
            this.getTableList();
        },

        //获取表格数据
        getTableList(){
            this.loading=true;
            this.axios.post("/proxy/ec/commission/getCommissionSubsidyListByGoods",{body:this.searchForm}).then(res=>{
                this.loading=false;
                let data=res.data;
                if(data.head.code=="10000"){
                    this.tableList=data.body.rows;
                    this.total = data.body.total;
                }
            })
        },
        //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.getTableList();
        },
        // 获取商品列表
        getGoodsList(){
            this.goodsLoading=true;
            this.axios.post("/proxy/ec/commission/getSubsidyGoodsList", {body: this.searchGoods}).then(res => {
                this.goodsLoading=false;
                let data=res.data;
                if(data.head.code=="10000"){
                    this.goodsList=data.body.goodsList;
                    this.goodsTotal = data.body.total;
                }
            })
        },
        search_smt_goods() {
            this.searchGoods.page.page = 1;
            this.getGoodsList();
        },
        //分页
        handleCurrentChangeGoods(val) {
            this.searchGoods.page.page = val;
            this.getGoodsList();
        },
        handleSizeChange(val){
            this.searchGoods.page.pageSize=val;
            this.getGoodsList();
        }, 
        handleSelectionChange(val){
            this.selectedGoods=val.map(item=>{
                return item.goodsId
            });
        },               
        doAction(type,data){
            switch(type){
                case 'add':
                    this.dialogGoods.type=type;
                    this.dialogGoods.title="商品维度调价比例新增";
                    this.dialogGoods.visible=true;
                    this.dialogGoods.step=0;
                     // 初始化数据
                    this.formGoodsComRate={
                        goodsIds:[],
                        commissionRate:'',
                        date:'',
                        startEffectiveDate:'',
                        endEffectiveDate:'',
                        cpaCodes:[]

                    };
                    this.searchGoods={
                        supplierCode:'',
                        goodsId:'',
                        goodsSn:'',
                        page: {
                            page: 1,
                            pageSize: 20
                        }
                    };
                    this.getGoodsList();
                    this.selectedGoods=[];
                    setTimeout(() => {
                        this.$refs.tableGoods.clearSelection();
                    }, 50);
                    
                    this.cpaChecked=[];
                    this.dialogGoods.url="/proxy/ec/commission/addCommissionSubsidyByGoods";
                    
                break;
                case 'edit':
                     this.dialogGoods.type=type;
                    // 初始化数据
                    this.dialogGoods.title="商品维度调价比例修改";
                    this.dialogGoods.visible=true;
                    this.dialogGoods.step=1;          
                    this.formGoodsComRate.commissionId=data.commissionId;
                    this.formGoodsComRate.supplierCode=data.supplierCode;
                    this.formGoodsComRate.goodsId=data.goodsId;
                    this.formGoodsComRate.goodsSn=data.goodsSn;
                    this.formGoodsComRate.commissionRate=data.commissionRate/100;
                    this.formGoodsComRate.date=[data.startEffectiveDate,data.endEffectiveDate];
                    this.cpaChecked=data.cpaCodes.split(',').map(item=>{
                        return parseInt(item)
                    })
                    this.dialogGoods.url="/proxy/ec/commission/updateCommissionSubsidyById";                                
                break;
                case "delete":
                    this.dialogGoods.type=type;
                    this.$confirm('确定要删除', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.axios.post("/proxy/ec/commission/deleteCommissionSubsidyById",{body:{commissionId:data.commissionId}}).then(res=>{
                            let data=res.data;
                            if(data.head.code=="10000"){
                                this.$message.success("删除成功");
                                this.getTableList();
                            }
                        })
                    }).catch(() => {
                        this.$message.info("已取消删除");         
                    });                    
                break;
                //商品选择
                case 'stepNext':
                    if(this.selectedGoods.length==0){
                        this.$message.warning("先选择商品！");
                        break;
                    }else{
                        this.dialogGoods.step=1;
                    }
                break;
            }
        },
        submit_dialog(){
            this.$refs.goodsform.validate(validate=>{
                if(!validate) return false
                if(this.$refs.cpa.cpaForm.cpaList.length==0){
                    this.$message.error("请选择CPA店铺");
                    return false
                }
                let params=JSON.parse(JSON.stringify(this.formGoodsComRate));
                if(params.commissionRate<0||params.commissionRate>7.5){
                    this.$message.error("降价比例范围(0-7.5%)")
                    return false
                }
                params.commissionRate=parseFloat(params.commissionRate)*100;
                params.startEffectiveDate=this.format("yyyy-MM-dd",new Date(params.date[0]));
                params.endEffectiveDate=this.format("yyyy-MM-dd",new Date(params.date[1]));
                params.cpaCodes=this.$refs.cpa.cpaForm.cpaList;
                params.goodsIds=this.selectedGoods;
                this.axios.post(this.dialogGoods.url,{body:params}).then(res=>{
                    let data=res.data;
                    if(data.head.code=="10000"){
                        this.$message.success("保存成功");
                        this.dialogGoods.visible=false;
                        this.getTableList();
                    }
                })
            })
            
            
        }

    },
    created(){       
        this.getTableList();
        this.getGoodsList();
    }
}
</script>

<style lang="less" scoped>
.search{
    padding-bottom: 10px;
}
.chunk-batch{
    margin-bottom:10px;
}
.do-action{
    span{
        color: #0F3FCF;
        cursor: pointer;
        margin:0 10px;
    }
}

</style>
<style lang="less">
.form-supplier{
    .form-tip{
        display: inline-block;
      
    }
    
    .el-form-item{
        margin-bottom:15px;
    }
    .commission-info{
        .el-form-item__content{
            width:150px;
        }
        .el-form-item{
            margin-bottom:0;
        }
    }
    .el-date-editor .el-range-separator{
        line-height: 24px;
    }
    
}

.dialog-goods{

    .el-dialog__header {
        padding: 8px 20px 10px;
    }
    .step-box{
        width: 100%;
        box-sizing:border-box;
        position:absolute;
        top:50px;
        left:0;
        padding:0 20px;
    }
    .el-dialog__body{
        max-height: 500px;
        margin-top:65px;
        margin-bottom:10px;
    }
    &.edit {
      .el-dialog__body{
          margin-top:5px;
      }  
    }
    .pagination {
        text-align: left;
        padding: 10px 0;
        position: relative;
        .el-pagination{
            display: inline-block;
        }
        .btn-next{
            position: absolute;
            top:10px;
            right: 10px;
        }
    }    
}
</style>





