<template>
    <div class="wrap wrap-commission-rate">
        <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>基础设置</el-breadcrumb-item>
                <el-breadcrumb-item>丰卖调价佣金比例管理</el-breadcrumb-item>
            </el-breadcrumb>
            <!--新增-->
         
        </div>
        <div class="chunk-tab" >
            <el-tabs v-model="filter.tabType"  >
                <el-tab-pane label=" 商 家 " name="supplier">
                    <comp-supplier :cpa-list="cpaList"></comp-supplier>
                </el-tab-pane>
                <el-tab-pane label=" 商 品 " name="goods">
                    <comp-goods :cpa-list="cpaList"></comp-goods>
                </el-tab-pane>              
            </el-tabs>
            <div class="time-tip">有效期开始时间为:00:00:00，结束时间为:23:59:59</div>
        </div>
    </div>
</template>
<script>
import CompSupplier from "@/components/sys/ec/commission-rate-supplier.vue";
import CompGoods from "@/components/sys/ec/commission-rate-goods.vue";
export default {
    components:{
        CompSupplier,
        CompGoods
    }, 
    data(){
        return {
            filter:{
                tabType:'supplier'
            },
            cpaList:[]
        }
    },
    methods:{
        //获取cpa列表
        getCpaList() {
            this.axios.post(this.myConfig.code.proxy + "/ec/cpa/partners/all", { body: {} }).then(response => {
                let data = response.data;
                if (data.head.code == "10000") {
                    this.cpaList = data.body.list;
                }
            });
        },       
    },
    created(){
        this.getCpaList();
    } 
}
</script>

<style lang="less" >
.wrap-commission-rate{
    padding:0 20px;
    .chunk-tab{
        position: relative;
        .time-tip{
            position: absolute;
            top:10px;
            right: 0;
        }
    }
    .el-form-item__content{
            width:auto;
    }
    .el-input--small,.el-date-editor.el-input, .el-date-editor.el-input__inner{
        width: 250px;
    }
}

.el-tooltip__popper{
    width:300px !important;
    // left:50% !important;
    margin-left: 60px;
}
.el-form-item__error{
    padding-left: 0;
    padding-top:0;
}
</style>



