<template>
    <div class="wrap-cpa-logistics">
    <!--面包导航-->
        <div class="nav-bar clear-fix">
            <el-breadcrumb separator=">" class="fl-l">
                <el-breadcrumb-item>合作商管理</el-breadcrumb-item>       
                <el-breadcrumb-item>关联区域物流</el-breadcrumb-item>       
            </el-breadcrumb>
            <!--新增-->
            <div class="add-menu fl-r">
                <el-button type="warning" class="add-menu-btn"  size="mini" @click="showDialog('cpaAdd')" v-if="authOps.includes('/ec/cpa-logistics/cpa-add')"> 新增合作商 </el-button>
                <el-button type="warning" class="add-menu-btn"  size="mini" @click="showDialog('logisticsAdd')" v-if="authOps.includes('/ec/cpa-logistics/logistics-add')"> 新增物流 </el-button>
            </div>

            <el-dialog :title="dialogTitle" :visible.sync="dialogFormCpa" class="dialogFormCpa" :inline="true"  :label-position="'right'">
                <el-form :model="formCpa" label-width="120px" ref="dialogFormCpa" >
                    <el-form-item label="合作商编号" prop="code" :rules="[{ required: true, message: '该字段必须填写' }, { type: 'integer', message: '必须为整数'},{pattern:/^[1-9][0-9]{3,}$/, message: '请输入大于1000'}]">
                        <el-input v-model.number="formCpa.code" size="mini" min="1000" :disabled="dialogType=='cpaUpdate'"></el-input>
                    </el-form-item>
                    <el-form-item label="合作商名称" prop="name" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-input v-model="formCpa.name" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item label="合作商排序" prop="sort" :rules="[{ required: true, message: '该字段必须填写' }, { type: 'number', message: '必须为数字值'}]">
                        <el-input v-model.number="formCpa.sort" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item label="推送币种" prop="currency" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-select v-model="formCpa.currency" size="mini" >
                            <el-option :label="item" :value="item" v-for="(item,index) in currencyList" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="平台状态" prop="active" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-select v-model="formCpa.active" clearable size="mini">
                            <el-option label="激活" :value="1"></el-option>
                            <el-option label="关闭" :value="0"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="是否第三方" prop="isThirdPartyApi" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-select v-model="formCpa.isThirdPartyApi" clearable size="mini">
                            <el-option label="是" :value="1"></el-option>
                            <el-option label="否" :value="2"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="自动转单" prop="conversionType" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-select v-model="formCpa.conversionType" clearable size="mini">
                            <el-option label="是" :value="1"></el-option>
                            <el-option label="否" :value="0"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="通信密钥" prop="authToken" :rules="[{ required: true, message: '该字段必须填写' },{min:6,message: '必须大于6个字符' }]">
                        <el-input v-model="formCpa.authToken" size="mini" :disabled="dialogType=='logisticsUpdate'"></el-input>
                    </el-form-item>
                    <el-form-item label="佣金系数" prop="coefficient" :rules="[{ required: true, message: '该字段必须填写' }, { type: 'number', message: '必须为数字值'},{pattern:/^[0-9](\.[0-9]{0,3})?$/, message: '请输入小于10'}]">
                        <el-input v-model.number="formCpa.coefficient" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item label="语言类型" prop="lang" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-select v-model="formCpa.lang" clearable size="mini">
                            <el-option label="所有" value="ALL"></el-option>
                            <el-option label="英语" value="EN"></el-option>
                            <el-option label="俄语" value="RU"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="扩展参数" >
                        <el-input v-model="formCpa.extendParams" type="textarea" size="mini"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer"  v-if="(authOps.includes('/ec/cpa-logistics/cpa-add')&&dialogType=='cpaAdd')||(authOps.includes('/ec/cpa-logistics/cpa-edit')&&dialogType=='cpaUpdate')">
                    <el-button @click="dialogFormCpa = false" size="small">取 消</el-button>
                    <el-button type="primary" @click="actionSubmit('cpa')" size="small">确 定</el-button>
                </div>
            </el-dialog>

            <el-dialog :title="dialogTitle" :visible.sync="dialogFormLogistics" class="dialogFormCpa" :inline="true"  :label-position="'right'">
                <el-form :model="formLogistics" label-width="120px"  ref="dialogFormLogistics" >                    
                    <el-form-item label="物流产品类型" prop="productType" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-select v-model="formLogistics.productType" size="mini" >
                            <el-option :label="item.label" :value="item.val" v-for="(item,index) in logisticsTypeList" :key="index"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="物流编号" prop="logisticsCode" :rules="[{ required: true, message: '该字段必须填写' }, { type: 'integer', message: '必须为数字值'}]" >
                        <el-input v-model.number="formLogistics.logisticsCode" size="mini" :disabled="dialogType=='cpaUpdate'"></el-input>
                    </el-form-item>
                    <el-form-item label="物流商(中文)" prop="logisticsCompany.cn" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-input v-model.number="formLogistics.logisticsCompany.cn" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item label="物流商(英文)" prop="logisticsCompany.en" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-input v-model.number="formLogistics.logisticsCompany.en" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item label="物流方式(中文)" prop="logisticsName.cn" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-input v-model.number="formLogistics.logisticsName.cn" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item label="物流方式(英文)" prop="logisticsName.en" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-input v-model.number="formLogistics.logisticsName.en" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item label="排序" prop="sort" :rules="[{ required: true, message: '该字段必须填写' }, { type: 'number', message: '必须为数字值'}]">
                        <el-input v-model.number="formLogistics.sort" size="mini"></el-input>
                    </el-form-item>
                    <el-form-item label="状态" prop="status" :rules="[{ required: true, message: '该字段必须填写' }]">
                        <el-select v-model="formLogistics.status" clearable size="mini">
                            <el-option label="有效" :value="0"></el-option>
                            <el-option label="作废" :value="-1"></el-option>
                        </el-select>
                    </el-form-item>
                    
                </el-form>
                <div slot="footer" class="dialog-footer" v-if="(authOps.includes('/ec/cpa-logistics/logistics-add')&&dialogType=='logisticsAdd')||(authOps.includes('/ec/cpa-logistics/logistics-edit')&&dialogType=='logisticsUpdate')" >
                    <el-button @click="dialogFormLogistics = false" size="small">取 消</el-button>
                    <el-button type="primary" @click="actionSubmit('logistics')" size="small">确 定</el-button>
                </div>
            </el-dialog> 

            <el-dialog :title="dialogTitle" :visible.sync="dialogFormArea" class="dialogFormCpa" :inline="true"  :label-position="'right'" >
                <div class="area-list">
                    <div class="item" v-for="item in areaList" :key="item.id">
                        <div class="label">{{item.areaName}}:</div>
                        <div class="content">
                            <span v-for="item2 in item.children" :key="item2.id">  
                                <el-radio  v-model="formArea.freightAreaId" :label="item2.id" size="mini">{{item2.areaName}}</el-radio >
                            </span>                          
                        </div>

                    </div>
                </div>
                <div slot="footer" class="dialog-footer" v-if="authOps.includes('/ec/cpa-logistics/area-edit')" >
                    <el-button @click="dialogFormArea = false" size="small">取 消</el-button>
                    <el-button type="primary" @click="actionSubmit('area')" size="small">确 定</el-button>
                </div>
            </el-dialog>                  

        </div>
       
        <div class="search report">
            <el-form :inline="true" :model="searchForm" class="demo-form-inline" label-width="80px">
                <el-form-item label="合作商" >
                    <el-select v-model="searchForm.cpaCode" size="small" clearable filterable>                              
                        <el-option :value="item.code" :label="item.name" v-for="item in cpaList" :key="item.code"></el-option>
                    </el-select>
                </el-form-item>
                <el-button type="primary" @click="search_smt" class="search-btn"> 查 询 </el-button>
                  
            </el-form>
        </div> 
        <div class="content">
            <el-table :data="cpaAreas" stripe style="width: 100%" v-loading="loading"  class="report" border max-height="450" @header-click="handleHeader">
                <el-table-column  label="合作商编号" width="100">
                    <template slot-scope="scope">{{scope.row.code}}</template>
                </el-table-column>
                <el-table-column  label="合作商"  width="200" align="center"> 
                    <template slot-scope="scope">
                        <span style="color:#409EFF;cursor:pointer;"  @click="showDialog('cpaUpdate',scope.row.code)">{{scope.row.name}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="对应区域"  width="100" class-name="column-area"> 
                    <template slot-scope="scope">
                        <div class="txt" style="color:#409EFF;cursor:pointer;"  @click="showDialog('areaUpdate',scope.row.freightAreaId,scope.row.code)">{{scope.row.areaName}}
                            &nbsp;&nbsp;
                        </div>
                    </template>
                </el-table-column>
                <el-table-column  v-for="item in logisticsList" :key="item.logisticsCode"  :prop="item.logisticsCode+''"  :label="item.logisticsName"   width="160" :xx="item.logisticsCode">
                    <template slot-scope="scope">
                        <el-input size="mini" v-model.trim="scope.row[item.logisticsCode+'']"></el-input>
                    </template>
                </el-table-column>
                <el-table-column fixed="right"  v-if="authOps.includes('/ec/cpa-logistics/logistics-order')">
                    <template slot-scope="scope">
                        <el-button @click="update(scope.row)" size="mini" type="primary" plain >修改</el-button>
                    </template>
                </el-table-column>
            </el-table>
                    <!-- 分页 -->
            <div class="pagination" v-if="Math.ceil(total/15)>1">
                <el-pagination @current-change="handleCurrentChange" :current-page="searchForm.page.page" :page-size="searchForm.page.pageSize" layout="total, prev, pager, next, jumper" :total="total">
                </el-pagination>
            </div>
        </div>         
                  
    </div>
</template>

<script>
import currencyList from '@/json/currency.json';
import logisticsTypeList from '@/json/logistics-type.json';
import { mapGetters } from 'vuex';
export default {
    data(){
        return {
            searchForm:{
                cpaCode:'',
                page: {
                    page: 1,
                    pageSize: 15
                }
            },
            total:'',
            logisticsList:[],
            cpaList:[],
            cpaAreas:[],
            loading:false,
            formCpa:{
                "code":"",
                "name":"",
                "active":"",
                "lang":"",
                "isThirdPartyApi":"",
                "currency":"",
                "coefficient":"",
                "authToken":"",
                "sort":"",
                "conversionType":"",
                "extendParams":""
            },
            formLogistics:{
                "logisticsCode":"",
                "productType":"",
                "status":"",
                "sort":"",
                "logisticsName":{
                    "en":"",
                    "cn":""
                },
                "logisticsCompany":{
                    "en":"",
                    "cn":""
                }
            },
            formArea:{
                freightAreaId:''
            },
            dialogFormCpa:false,
            dialogFormLogistics:false,
            dialogFormArea:false,
            currencyList:currencyList.data,
            logisticsTypeList:logisticsTypeList.data,
            dialogTitle:'',
            dialogUrl:'',
            dialogType:'',
            areaList:[]
        }
    },
    computed:{
        ...mapGetters({
            authOps: 'getAuthOps'
        })
    },
    methods:{
        handleHeader(column, event){
            let code=column.property;
            if(code){
                this.showDialog("logisticsUpdate",code);
            }            
        },
        update(data){
            let strArr=['name',"freightAreaId","code","areaName"]
            let params={
                cpaCode:data.code,
                logisticsCpaData:[]
            }
            let reg=/[^0-9\s]/;
            for(let i in data){          
                if(!strArr.includes(i)){
                              
                    if(reg.test(data[i])){
                        console.log(data[i],444)
                        this.$message.error("权重必须是数字");
                        return false;
                    }
                    params.logisticsCpaData.push({logisticsTypeCode:i,weight:data[i]})
                }
            }
            this.axios.post('/proxy/freight/api/freight/logistics/cpa/save',{body:params}).then(res=>{
                if(res.data.head.code=='10000'){
                    this.$message.success("修改成功");
                    this.renderCpaLogistics();
                }
            })
        },
        //显示dialog
        showDialog(type,data,data2){
            this.dialogType=type;
            switch(type){
                case 'cpaAdd':
                    this.formCpa={
                        "code":"",
                        "name":"",
                        "active":"",
                        "lang":"",
                        "isThirdPartyApi":"",
                        "currency":"",
                        "coefficient":"",
                        "authToken":"",
                        "sort":"",
                        "conversionType":"",
                        "extendParams":""
                    }
                    this.dialogTitle="新增合作商";
                    this.dialogFormCpa=true;
                    if(this.$refs.hasOwnProperty('dialogFormCpa')){
                        this.$refs.dialogFormCpa.resetFields();
                    }
                    this.dialogUrl="/proxy/ec/cpa/partners/insert";
                break;
                case 'cpaUpdate':
                    if(!this.authOps.includes('/ec/cpa-logistics/cpa-show')) return false
                    this.axios.post('/proxy/ec/cpa/partners/findByCode',{body:{code:data}}).then(res=>{
                        let data=res.data;
                        if(data.head.code=="10000"){
                            this.dialogFormCpa=true;
                            if(this.$refs.hasOwnProperty('dialogFormCpa')){
                                this.$refs.dialogFormCpa.resetFields();
                            }                            
                            this.formCpa=data.body;
                            this.dialogTitle="修改合作商";
                            
                            this.dialogUrl="/proxy/ec/cpa/partners/update";
                        }
                    });                   
                break;
                case 'logisticsAdd':
                    this.formLogistics={
                        "logisticsCode":"",
                        "productType":"",
                        "status":"",
                        "sort":"",
                        "logisticsName":{
                            "en":"",
                            "cn":""
                        },
                        "logisticsCompany":{
                            "en":"",
                            "cn":""
                        }
                    }
                    this.dialogTitle="新增物流";
                    this.dialogFormLogistics=true;
                
                    if(this.$refs.hasOwnProperty('dialogFormLogistics')){
                        this.$refs.dialogFormLogistics.resetFields();
                    } 
                    this.dialogUrl="/proxy/freight/api/freight/logisticstype/insert";
                break;
                case 'logisticsUpdate': 
                    if(!this.authOps.includes('/ec/cpa-logistics/logistics-show')) return false;                  
                    this.axios.post('/proxy/freight/api/freight/logisticstype/findByCode',{body:{logisticsCode:data}}).then(res=>{
                        let data=res.data;
                        if(data.head.code=="10000"){
                            this.dialogFormLogistics=true;
                            if(this.$refs.hasOwnProperty('dialogFormLogistics')){
                                this.$refs.dialogFormLogistics.resetFields();
                            }                            
                            this.formLogistics=data.body;
                            this.dialogTitle="修改物流";
                            this.dialogUrl="/proxy/freight/api/freight/logisticstype/update";
                        }
                    });
                    
                break;
                case 'areaUpdate':
                    if(!this.authOps.includes('/ec/cpa-logistics/area-show')) return false;
                    this.dialogUrl="/proxy/freight/api/areacpa/save";
                    this.formArea.freightAreaId=data;
                    this.formArea.cpaCode=data2;
                    this.dialogTitle="关联国家区域";
                    this.dialogFormArea=true;
                    
                break;
            }
        },
        actionSubmit(type){
            switch(type){
                case 'cpa':
                    this.$refs.dialogFormCpa.validate((valid) => {
                        if(!valid) return false;
                        this.axios.post(this.dialogUrl,{body:this.formCpa}).then(res=>{
                            let data=res.data;
                            if(data.head.code=="10000"){
                                this.dialogFormCpa=false;
                                this.$message.success("添加成功");                       
                                this.renderCpaLogistics();
                                this.getCpaList();
                            }
                        })
                    })
                break;
                case 'logistics':
                    
                    this.$refs.dialogFormLogistics.validate((valid) => {
                        if(!valid) return false;
                        this.axios.post(this.dialogUrl,{body:this.formLogistics}).then(res=>{
                            let data=res.data;
                            if(data.head.code=="10000"){
                                this.dialogFormLogistics=false;
                                this.$message.success("添加成功");
                                this.axios.post("/proxy/logistics/redis/refresh",{body:{}}).then(res2=>{
                                    if(res2.data.head.code=="10000"){
                                        this.renderCpaLogistics();
                                    }
                                })                       
                                
                            }
                        })
                    })
                break;
                case 'area':
                    
                    if(this.formArea.freightAreaId==""){
                        this.$message.error("请选地区");
                        break;
                        return false
                    }
                    this.axios.post(this.dialogUrl,{body:this.formArea}).then(res=>{
                        let data=res.data;
                        if(data.head.code=="10000"){
                            this.dialogFormArea=false;
                            this.$message.success("添加成功");
                            this.renderCpaLogistics();
                        }
                    })
                break;
            }
        },
        search_smt(){
            this.searchForm.page.page = 1;
            this.renderCpaLogistics();
        },
        // 获取物流方式
        getLogistics() {
            return new Promise((resolve,reject)=>{
                this.axios.post(this.myConfig.code.proxy + "/logistics/logistics/queryType", { body: { languageCode: "cn" } }).then(response => {
                    let data = response.data;
                    if (data.head.code == "10000") {
                        this.logisticsList = data.body.logisticsTypes;
                        resolve(this.logisticsList);
                    }
                });
            })

        },        
        async renderCpaLogistics(){ 
            this.loading=true;                  
            let res1=await this.getLogistics();
            let res2=await this.getCpaArea(res1);
            this.loading=false;
        },
            //分页
        handleCurrentChange(val) {
            this.searchForm.page.page = val;
            this.renderCpaLogistics();
        },
        //获取cpa列表
        getCpaList() {        
            this.axios.post("/proxy/ec/cpa/partners/all", { body: { } }).then(res => {
                let data = res.data;
                if (data.head.code == "10000") {
                    this.cpaList = data.body.list;
                    this.cpaListAll=[];
                    for(let item of [...this.cpaList].values()){
                        this.cpaListAll.push(item.code)
                    }
                }
            });
        },
        //获取cpa对应地区物流列表
        getCpaArea(res1){
            return new Promise((resolve,reject)=>{
                
                this.axios.post("/proxy/freight/api/areacpa/all", { body:this.searchForm }).then(res => {
                    let data = res.data;
                    if (data.head.code == "10000") {
                        let list=data.body.rows;
                        this.total = data.body.total;                        
                        for(let i in list){                            
                            for(let j in this.logisticsList){
                                list[i][this.logisticsList[j].logisticsCode]=""
                            }
                            for(let k in list[i].logisticsData){
                                list[i][list[i].logisticsData[k].logisticsTypeCode]=list[i].logisticsData[k].weight
                            }
                            delete list[i].logisticsData
                        }
                        this.cpaAreas=list                 
                        resolve()
                        
                    }
                });
            })
        },
        getAreaList(){
            this.axios.post("/proxy/freight/api/area/list",{body:{}}).then(res=>{
                let data=res.data;
                if(data.head.code=="10000"){
                    this.areaList=data.body.areas;
                }
            })
        }               
    },
    created(){
        this.getCpaList();
        this.getAreaList();
        this.renderCpaLogistics();
          
        
    }
}
</script>

<style lang="less">
.wrap-cpa-logistics{
    padding-right: 15px;
    .el-form-item,.search-btn{
        display: inline-block;
        vertical-align: middle;
    }
    .dialogFormCpa{
        .el-form-item {
            margin-bottom: 15px;
            width: 310px;
        }
    }
    .el-form-item__error{
        padding-top:0;
        padding-left:0;
    }
    .el-table th{
        user-select:inherit;
        cursor: pointer;
    }
    .el-dialog__body{
        padding:10px 15px;
        max-height: 470px;
        overflow-y: auto;

    }
    .area-list{
        .item{
            position: relative;
            padding-bottom:10px;
            padding-top: 0;
            
            .label{
                position: absolute;
                top:0;
                left: 0;
                width: 100px;
                text-align: right;
                
            }
            .content{
                padding-left: 110px;
            }
            .el-radio{
                width: 135px;
                margin-right: 10px;
            }

        }
    }
    .column-area{
        height:100%;
        .txt{
            // padding-top:5px;
        }
        td .cell,.txt{
            width: 100%;
            height:100%;
            
            text-align: center;
            // min-height: 26px;
        }
    }

}
</style>



